import React from 'react';
import '../../styles/Quad.scss'
import { Redirect } from 'react-router-dom';
import getStructure from '../Structure'

const Quad = (props) => {
    const data = props.location.data !== undefined ? JSON.parse(props.location.data) : [1, 2, 3, 4]

    return (
        <div className="container-quad">
            {props.location.data === undefined ? <Redirect to="/quad-form" /> : getStructure(data)}
        </div>
    );
}

export default Quad;