import React from 'react';
import StreamBox from './elements/StreamBox';
import ChatBox from './elements/ChatBox';
import Placeholder from './elements/Placeholder';

function getStructure(data) {
    const coords = [
        { col: 1, row: 1 },
        { col: 2, row: 1 },
        { col: 1, row: 2 },
        { col: 2, row: 2 }
    ]
    const fields = data.map((item, id) => {
        if (item.type === "empty") {
            return <Placeholder />
        }
        else if (item.field === "stream") {
            return <StreamBox id={id} type={item.type} src={item.src} area={coords[id]} key={item.id} />
        }
        else if (item.field === "chat") {
            return <ChatBox id={id} type={item.type} src={item.src} area={coords[id]} key={item.id} />
        }
        else {
            return <Placeholder />
        }
    })
    return fields;
}

export default getStructure;