import React from 'react';
import '../../styles/ChatBox.scss';

const ChatBox = ({ id, type, src, area }) => {
    const style = { gridColumnStart: area.col, gridRowStart: area.row, gridColumn: area.col2 }

    const getSrc = () => {
        if (type === "youtube") {
            return `https://www.youtube.com/live_chat?v=${src}&amp;embed_domain=${window.location.hostname}`
        } else if (type === "twitch") {
            return `https://www.twitch.tv/embed/${src}/chat?darkpopout`
        }
    }
    return (
        <div className="chat-box" style={style}>
            <iframe title={id} src={getSrc()} frameBorder="0" scrolling="no"></iframe>
        </div>
    );
}

export default ChatBox;