import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css'
import './App.scss';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import MainMenu from './components/MainMenu';
import ErrorPage from './components/ErrorPage';
import Form from './components/Form';
import Double from './components/layouts/Double';
import Quad from './components/layouts/Quad';


class App extends Component {
  state = {
  }

  render() {
    return (
      <div className="App">
        <Router basename={process.env.PUBLIC_URL}>
          <Switch>
            <Route path="/" exact component={MainMenu} />
            <Route path="/double-form" component={Form} />
            <Route path="/quad-form" component={Form} />
            <Route path="/double/" component={Double} />
            <Route path="/quad/" component={Quad} />
            <Route component={ErrorPage} />
          </Switch>
        </Router>
      </div>
    );
  }
}

export default App;
