import React, { Component } from 'react';
import '../styles/Form.scss'
import { Redirect, Route } from 'react-router-dom';
import Field from './Field'

class Form extends Component {

    ytRegEx = /(?:.+?)?(?:\/v\/|watch\/|\?v=|\&v=|youtu\.be\/|\/v=|^youtu\.be\/|watch\%3Fv\%3D)([a-zA-Z0-9_-]{11})+/
    twitchRegEx = /^(?:https?:\/\/)?(?:www\.|go\.)?twitch\.tv\/([a-z0-9_]+)($|\?)/

    state = {
        field1: 'stream',
        src1: '',

        field2: 'stream',
        src2: '',

        field3: 'stream',
        src3: '',

        field4: 'stream',
        src4: '',

        isSet: false
    }

    link_data = {}

    testLink() {
        if (arguments.length > 0) {
            let finalArray = []
            for (let i = 0; i < arguments.length; i++) {
                if (this.ytRegEx.test(arguments[i])) {
                    const match = this.ytRegEx.exec(arguments[i])
                    finalArray.push(["youtube", match[1]])
                }
                else if (this.twitchRegEx.test(arguments[i])) {
                    const match = this.twitchRegEx.exec(arguments[i])
                    finalArray.push(["twitch", match[1]])
                } else if (arguments[i] === "empty") {
                    finalArray.push(["empty", ""])
                }
                else {
                    return [false, "Invalid data!"];
                }
            }
            return [true, ...finalArray];
        }
        else {
            return [false, "Links validation error!"]
        }
    }

    handleFormSubmit = (e) => {
        const { field1, src1, field2, src2, field3, src3, field4, src4 } = this.state
        e.preventDefault();
        if (this.props.match.path === "/double-form") {
            const validate = this.testLink(src1, src2);
            if (validate[0]) {
                this.link_data = [
                    {
                        id: 1,
                        field: field1,
                        type: validate[1][0],
                        src: validate[1][1]
                    },
                    {
                        id: 2,
                        field: field2,
                        type: validate[2][0],
                        src: validate[2][1]
                    }
                ]
                this.setState({
                    isSet: true
                })
            }
            else if (!validate[0]) {
                alert(validate[1]);
            }
        }
        else if (this.props.match.path === "/quad-form") {
            const validate = this.testLink(src1, src2, src3, src4);
            if (validate[0]) {
                this.link_data = [
                    {
                        id: 1,
                        field: field1,
                        type: validate[1][0],
                        src: validate[1][1]
                    },
                    {
                        id: 2,
                        field: field2,
                        type: validate[2][0],
                        src: validate[2][1]
                    },
                    {
                        id: 3,
                        field: field3,
                        type: validate[3][0],
                        src: validate[3][1]
                    },
                    {
                        id: 4,
                        field: field4,
                        type: validate[4][0],
                        src: validate[4][1]
                    }
                ]
                this.setState({
                    isSet: true
                })
            }
            else if (!validate[0]) {
                alert(validate[1]);
            }
        }
    }

    handleOnChange = (e) => {
        this.setState({
            [e.target.name]: e.target.value,
        })
    }

    render() {

        const labels = [<div key={1} className="field-type">Field type</div>, <div key={2} className="input_src">Source</div>]

        return (
            <>
                <form className="form4-container" onSubmit={this.handleFormSubmit}>
                    <Route path="/double-form" render={() => {
                        if (this.state.isSet) {
                            return <Redirect to={{
                                pathname: `/double`,
                                data: JSON.stringify(this.link_data)
                            }} />
                        }
                        return (
                            <div className="form2">
                                {labels}
                                <Field field={this.state.field1} nameID={1} change={this.handleOnChange} />
                                <Field field={this.state.field2} nameID={2} change={this.handleOnChange} />
                            </div>
                        )
                    }} />
                    <Route path="/quad-form" render={() => {
                        if (this.state.isSet) {
                            return <Redirect to={{
                                pathname: `/quad`,
                                data: JSON.stringify(this.link_data)
                            }} />
                        }
                        return (
                            <div className="form4">
                                {labels}
                                <Field field={this.state.field1} nameID={1} change={this.handleOnChange} />
                                <Field field={this.state.field2} nameID={2} change={this.handleOnChange} />
                                <Field field={this.state.field3} nameID={3} change={this.handleOnChange} />
                                <Field field={this.state.field4} nameID={4} change={this.handleOnChange} />
                            </div>
                        )
                    }} />
                    <button type="submit" className="btn btn-success">Start watching!</button>
                </form>
            </>
        );
    }
}

export default Form;