import React from 'react';

const Field = (props) => {
    return (
        <>
            <select className="form-control field-type" name={"field" + props.nameID} value={props.field.field} onChange={props.change}>
                <option value="stream">Stream</option>
                <option value="chat">Chat</option>
            </select>
            <input
                type="text"
                className="form-control input_src"
                name={"src" + props.nameID}
                placeholder="Paste a youtube/twitch link or type 'empty'!"
                value={props.field.src}
                onChange={props.change}
            />
        </>
    );
}

export default Field;