import React from 'react';
import '../../styles/Double.scss'
import { Redirect } from 'react-router-dom';
import getStructure from '../Structure'

const Double = (props) => {
    const data = props.location.data !== undefined ? JSON.parse(props.location.data) : [1, 2, 3, 4];

    return (
        <div className="container-double">
            <div className="streams-grid">
                {props.location.data === undefined ? <Redirect to="/double-form" /> : getStructure(data)}
            </div>
        </div>
    );
}

export default Double;