import React from 'react';
import '../../styles/StreamBox.scss';

const StreamBox = ({ id, type, src, area }) => {

    const style = { gridColumnStart: area.col, gridRowStart: area.row }

    const getSrc = () => {
        if (type === "youtube") {
            return `https://www.youtube.com/embed/${src}?autoplay=1`
        } else if (type === "twitch") {
            return `https://player.twitch.tv/?channel=${src}`
        }
    }
    return (
        <div className="stream-box" style={style}>
            <iframe title={id} src={getSrc()} scrolling="no" frameBorder="0"></iframe>
        </div>
    );
}

export default StreamBox;