import React from 'react';
import '../styles/MainMenu.scss'
import { Link } from 'react-router-dom';

const MainMenu = () => {
    return (
        <div className="main-menu">
            <h1>StreamHUB</h1>
            <div className="menu">
                <Link to="/double-form">
                    <div className="menu_option">
                        <p>2 Fields</p>
                    </div>
                </Link>
                <Link to="/quad-form">
                    <div className="menu_option">
                        <p>4 Fields</p>
                    </div>
                </Link>
            </div>
        </div>
    );
}

export default MainMenu;